<template>
  <div>
    <gmap-map
      :center="center"
      :zoom="10.75"
      :options="mapOptions"
      style="width: 100%; height: 500px"
      ref="baseMap"
      @click="getLatLong"
    >
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :title="m.title"
        @click="toggleInfoWindow(m, index)"
        :option="MarkerOptions"
        :icon="`${publicPath}marker/favicon.ico`"
      />
      <!-- <gmap-marker :position="mapStartLocation" :draggable="true" @dragend="updateCoordinates" /> -->
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen"
        @closeclick="infoWinOpen=false"
      >
        <div v-html="infoContent"></div>
      </gmap-info-window>
    </gmap-map>
  </div>
</template>
<script>
// import { component } from 'vue/types/umd';
// import {gmapApi} from 'vue2-google-maps';
export default {
  data() {
    return {
      publicPath: process.env.BASE_URL,
      mapStartLocation:{lat: 24.9004057, lng: 67.1926178},
        coordinates: null,
        center:{lat: -6.2379957, lng: 107.0782953},
        mapOptions:{
          fullscreenControl:false,
          zoomControl: true,
          mapTypeControl: false,
          scaleControl: false,
          streetViewControl: false,
          rotateControl: false,
          disableDefaultUi: false,
        },
        map: null,
        infoContent: '',
        infoWindowPos: {
          lat: 0,
          lng: 0
        },
        infoWinOpen: false,
        currentMidx: null,
        fullscreenControl:false,
        infoOptions: {
          pixelOffset: {
            width: 0,
            height: -35
          }
        },  
        MarkerOptions:{
          zIndex:999999,
          opacity:0.2

        },
        markers: 
        [
          {
            name: "SEGER PEJUANG",
            title: "SEGER PEJUANG",
            location: "location 1",
            position:  { lat: -6.1827041, lng: 106.9861642 }
          },
           {
            name: "SEGERRR THB",
            title: "SEGERRR THB",
            location: "location 2",
            position:  { lat: -6.1769756, lng: 106.9958617 }
          },
          {
            name: "SEGERRR TJ.PRIOK",
            title: "SEGERRR TJ.PRIOK",
            location: "location 3",
            position: { lat: -6.1173661, lng: 106.8808052 }
          },
          {
            name: "SEGERRR HARAPAN JAYA",
            title: "SEGERRR HARAPAN JAYA",
            location: "location 3",
            position: { lat: -6.2136376, lng: 106.983019 }
          }
        ],
      };
    },
    // computed: { google: gmapApi } ,
    // mounted() {
    //   this.$refs.baseMap.$mapPromise.then(map => {

    //   const trafficLayer = this.google.maps.TrafficLayer()
    //   trafficLayer.setMap(map)
    //   })
    // },

    methods: {  
      updateCoordinates(location) {
                let lat = location.latLng.lat();
                let lng = location.latLng.lng();
            alert("You clicked the map at " + lat + "," + lng);
      },

      getLatLong(event){
        // alert('ok')
        var lat = event.latLng.lat().toFixed(4);

        var lng = event.latLng.lng().toFixed(4);              

        // alert("You clicked the map at " + lat + "," + lng);
        this.$swal.fire({
          title: '<h3><strong>Apakah Anda Yakin ?</strong></h3>',
          html: '<span class="text-center">Ingin Melihat Traffic Di Titik LatLong <br><strong>' + lat + ' , ' + lng +'</strong> Yang Anda Klik</span>',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Ya'
        }).then((result) => {
          if (result.value) {
            window.open("https://www.google.com/maps/place/" + lat + "," + lng +"/@" + lat + "," + lng +",17z/data=!3m1!4b1!4m5!3m4!1s0x0:0x0!8m2!3d" + lat + "!4d" + lng + "!5m1!1e1","_blank")
          }
        })
      },
      
      toggleInfoWindow(marker, idx) {
        this.infoWindowPos = marker.position;
        this.infoContent = this.getInfoWindowContent(marker);

        //check if its the same marker that was selected if yes toggle
        if (this.currentMidx == idx) {
          this.infoWinOpen = !this.infoWinOpen;
        }
        //if different marker set infowindow to open and reset current marker index
        else {
          this.infoWinOpen = true;
          this.currentMidx = idx;
        }
      },
      
      getInfoWindowContent(marker) {
        return (
        `<div class="">
          <div>
            <div>
              <div class="m-2"><span style="font-weight: bold;">Name Store: </span>
                ${marker.name}
              </div>
            </div>
            <div class="m-2"><span style="font-weight: bold;">Location:  </span>
              ${marker.location}
              <br>
            </div>
          </div>
        </div>`);
      },
    }
  };
</script>